import React from 'react'
import Layout from '../../components/main-layout-en';
import { navigate } from "gatsby";
import { Helmet } from "react-helmet"

import CommunityDefaultImage from '../../images/thumb/community.png';
import EventDefaultImage from '../../images/thumb/event.png';
import ListBg from '../../images/bg/list.svg';
import ListBgSp from '../../images/bg/list-sp.svg'
import NewsDefaultImage from '../../images/thumb/news.png';
import PromotionDefaultImage from '../../images/thumb/promotion_council.png';
import TaskForceDefaultImage from '../../images/thumb/task_force.png';
import UseCaseDefaultImage from '../../images/thumb/use_case.png';
import WhitePaperDefaultImage from '../../images/thumb/white_paper.png';

export default function article({ 
    pageContext: {
        id,
        title,
        body,
        category,
        published_at,
        thumbnail
    }
}) {

  const date = new Date(published_at);
  const formattedDate = date.toLocaleDateString('ja-JP', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
  });
  const desiredFormat = formattedDate.replace(/(\d+)\/(\d+)\/(\d+)/, '$1/$2/$3');
  let defaultImage;

  switch(category) {
    case 'お知らせ':
      defaultImage = NewsDefaultImage;
      break;
    case 'ユースケース':
      defaultImage = UseCaseDefaultImage;
      break;
    case 'イベント':
      defaultImage = EventDefaultImage;
      break;
    case '推進協議会':
      defaultImage = PromotionDefaultImage;
      break;
    case 'ホワイトペーパー':
      defaultImage = WhitePaperDefaultImage;
      break;
    case 'タスクフォース':
      defaultImage = TaskForceDefaultImage;
      break;
    case 'コミュニティ':
      defaultImage = CommunityDefaultImage;
      break;
    default:
  }

  return (
    <Layout>
      <Helmet>
        <title>{title}</title>
      </Helmet>
      <section className="detail">
        <picture>
          <source media="(min-width: 768px)" srcSet={ListBg} />
          <img className="list__bg" src={ListBgSp} alt="お知らせ" />
        </picture>
        <div className="default-max-width">
          <div className="detail__header-container">
            <h2 className="heading heading--no-line">{ title }</h2>
            <div className="news__date-container">
              <span className="news__date" suppressHydrationWarning={true}>{ desiredFormat }</span>
              <span className="tag tag--blue">{ category }</span>
            </div>
          </div>
          <div className="detail__content-container">
            <div className="detail__image-container">
              {
                thumbnail
                  ? <img src={thumbnail.url} className="detail__image" alt={ title } />
                  : <img src={defaultImage} className="detail__image" alt={ title } />
              }
            </div>
            <div className="detail__content" dangerouslySetInnerHTML={{ __html: body }} />
          </div>
          <div className="detail__button-container">
            <button onClick={() => navigate(-1)} className="button">Back to list</button>
          </div>
        </div>
      </section>
    </Layout>
  )
}
